<script>
import { ref } from 'vue'

export default {
  setup() {
    const model = process.env.VUE_APP_MODE
    const classList = ref(["tab-link", "tab-link", "tab-link", "tab-link"])
    //get url id
    const getUrlString = location.href;
    const url = getUrlString.split("/");
    if(url.includes('caregiver')){
    classList.value = ["tab-link active", "tab-link", "tab-link", "tab-link"]
    }else if(url.includes('work')){
      classList.value = ["tab-link active", "tab-link", "tab-link", "tab-link"]
    }else if(url.includes('schedule')){
      classList.value = ["tab-link", "tab-link active", "tab-link", "tab-link"]
    }else if(url.includes('faq')){
      classList.value = ["tab-link", "tab-link", "tab-link active", "tab-link"]
    }else if(url.includes('member')){
      classList.value = ["tab-link", "tab-link", "tab-link", "tab-link active"]
    }
    return { classList,model };
  }
};
</script>

<template>
  <footer class="c-footer">
    <div class="row ui-tabbar">
      <div class="col" v-if="(model == 'purple')">
        <a @click="$router.push({ path: '/caregiver/list' })" :class="classList[0]">
          <div class="tab-img"><i class="far fa-search"></i></div>
          <div class="tab-title">搜尋資料</div>
        </a>
      </div>
      <div class="col" v-if="(model == 'pink')">
        <a @click="$router.push({ path: '/work/list' })" :class="classList[0]">
          <div class="tab-img"><i class="far fa-search"></i></div>
          <div class="tab-title">搜尋工作</div>
        </a>
      </div>
      <div class="col">
        <a @click="$router.push({path:'/schedule/list'})" :class="classList[1]"
          ><div class="tab-img"><i class="far fa-calendar"></i></div>
          <div class="tab-title">簽到打卡</div></a
        >
      </div>
      <div class="col">
        <a @click="$router.push({ path: '/home' })" class="tab-link">
          <div class="tab-img"><i class="fas fa-home-lg"></i></div>
          <div class="tab-title">回首頁</div>
        </a>
      </div>
      <!-- <div class="col">
        <a @click="$router.push({path:'/help/list'})" :class="classList[2]"
          ><div class="tab-img"><i class="far fa-question-circle"></i></div>
          <div class="tab-title">使用教學</div></a
        >
      </div> -->
      <div class="col">
        <a @click="$router.push({ path: '/faq' })" :class="classList[2]">
          <div class="tab-img"><i class="far fa-question-circle"></i></div>
          <div class="tab-title">常見問題</div>
        </a>
      </div>
      <div class="col">
        <a @click="$router.push({ path: '/member' })" :class="classList[3]">
          <div class="tab-img"><i class="far fa-user"></i></div>
          <div class="tab-title">會員專區</div>
        </a>
      </div>
    </div>
  </footer>
</template>

